
"use client";

import * as React from 'react';
import { Suspense } from 'react';
import { Package, Shapes, DollarSign, PackageX } from 'lucide-react';
import type { Item, Category } from '@/types';
import { StatCard } from '@/components/dashboard/stat-card';
import { StockChart } from '@/components/dashboard/stock-chart';
import { AIAlert } from '@/components/dashboard/ai-alert';
import { Skeleton } from '@/components/ui/skeleton';
import { PageHeader } from '@/components/page-header';

export default function DashboardPage() {
  const [items, setItems] = React.useState<Item[]>([]);
  const [categories, setCategories] = React.useState<Category[]>([]);

  React.useEffect(() => {
    try {
      const storedItems = localStorage.getItem('inventopro-items');
      if (storedItems) {
        setItems(JSON.parse(storedItems));
      }
      const storedCategories = localStorage.getItem('inventopro-categories');
      if (storedCategories) {
        setCategories(JSON.parse(storedCategories));
      }
    } catch (error) {
        console.error("Failed to parse from localStorage", error);
    }
  }, []);

  const totalItems = items.reduce((sum, item) => sum + item.quantity, 0);
  const lowStockItemsCount = items.filter(item => item.quantity <= item.reorderPoint).length;
  const totalValue = items.reduce((sum, item) => sum + item.quantity * item.price, 0);
  const categoryCount = categories.length;

  const lowStockForAI = items.filter(item => item.quantity <= item.reorderPoint);

  return (
    <>
      <PageHeader title="Dashboard" description="An overview of your inventory." />
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
        <StatCard
          title="Total Items"
          value={totalItems.toLocaleString()}
          icon={Package}
        />
        <StatCard
          title="Low Stock Items"
          value={lowStockItemsCount.toString()}
          icon={PackageX}
        />
        <StatCard
          title="Inventory Value"
          value={`$${totalValue.toLocaleString('en-US', {
            minimumFractionDigits: 2,
            maximumFractionDigits: 2,
          })}`}
          icon={DollarSign}
        />
        <StatCard
          title="Categories"
          value={categoryCount.toString()}
          icon={Shapes}
        />
      </div>
      <div className="grid gap-4 md:grid-cols-7">
        <div className="md:col-span-4">
            <StockChart items={items} />
        </div>
        <div className="md:col-span-3">
          <Suspense fallback={<Skeleton className="h-full w-full" />}>
            <AIAlert lowStockItems={lowStockForAI} />
          </Suspense>
        </div>
      </div>
    </>
  );
}
