import { generateStockAlert } from '@/ai/flows/generate-stock-alert';
import type { Item } from '@/types';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { AlertTriangle } from 'lucide-react';
import { Badge } from '@/components/ui/badge';

type AIAlertProps = {
  lowStockItems: Item[];
};

export async function AIAlert({ lowStockItems }: AIAlertProps) {
  if (lowStockItems.length === 0) {
    return null;
  }

  const alertInput = {
    lowStockItems: lowStockItems.map(item => ({
      itemId: item.id,
      itemName: item.name,
      currentQuantity: item.quantity,
      reorderPoint: item.reorderPoint,
      averageDailySales: item.averageDailySales,
      sellingPrice: item.price,
      supplierName: item.supplierName,
      leadTimeDays: item.leadTimeDays,
    })),
  };

  try {
    const { alertSummary } = await generateStockAlert(alertInput);

    return (
      <Card className="border-primary/50 bg-primary/5">
        <CardHeader>
            <div className="flex items-center gap-3">
                <AlertTriangle className="h-6 w-6 text-primary" />
                <CardTitle className="font-bold text-primary">Intelligent Stock Alert</CardTitle>
                <Badge variant="default" className="ml-auto">AI-Powered</Badge>
            </div>
          <CardDescription className="pt-2 text-primary/90">
            AI-analysis of current low-stock items.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <p className="text-sm whitespace-pre-wrap">{alertSummary}</p>
        </CardContent>
      </Card>
    );
  } catch (error) {
    console.error('Error generating AI stock alert:', error);
    return (
        <Card className="border-destructive/50 bg-destructive/5">
            <CardHeader>
                <div className="flex items-center gap-3">
                    <AlertTriangle className="h-6 w-6 text-destructive" />
                    <CardTitle className="font-bold text-destructive">Could not generate AI Alert</CardTitle>
                </div>
            </CardHeader>
            <CardContent>
                <p className="text-sm text-destructive">There was an issue with the AI service. Please try again later.</p>
            </CardContent>
        </Card>
    )
  }
}
